<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <acceptance v-if="currentStep === steps.findIndex(step=>step.key === 'acceptance')"/>
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'modelPreview')"
            :handle-business="getModelPre"
        />
        <person-info v-if="currentStep === steps.findIndex(step=>step.key === 'personInfo')"/>
        <process
            :type="3"
            v-if="currentStep === steps.findIndex(step=>step.key === 'print')"
            :handle-business="handlePrint"
            wait-text="正在打印中，请稍等"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            page-title="办理完成"
            :data="relationCompleteRes"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import personInfo from './components/person-info';
import acceptance from './components/acceptance';
import process from '@/components/process';
import complete from '@/components/complete';
import { saveFaceReco, proveComplete } from '../../../../service/human/prove';
import { produceModelReq, toPrint } from '@/service/common';
import { getPrintPrefix } from '@/utils';

export default {
    name: 'RelationshipIndex', // 亲属关系身份证明
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        personInfo,
        acceptance,
        process,
        complete
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera',
            businessTypeCode: 'businessTypeCode',
            printId: 'printId'
        }),
        ...mapState('relation', {
            relationRes: 'relationRes'
        })
    },
    data () {
        return {
            relationCompleteRes: {} // 亲属关系证明完成返回结果
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'setSteps', 'changeTipPage', 'setPrintInfo']),
        async handleBusiness () {
            const res = await saveFaceReco({
                businessType: 'qsgxzm', // 业务类型 qsgxzm 亲属关系证明
                idNum: this.identifyCard.certNumber,
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async getModelPre () {
            // 获取模板预览图片
            const dateStr = this.$moment(new Date()).format('YYYY年MM月DD日');
            const res = await produceModelReq({
                code: 'qsgxzm', // 亲属关系证明
                body: {
                    ...this.relationRes,
                    csrq: this.$moment(this.relationRes.csrq).format('YYYY年MM月DD日'),
                    date: dateStr
                }
            });
            if (res && res.code === '0') {
                this.setPrintInfo(res.result);// 预览所需图片Id
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handlePrint () {
            // 打印证明
            const res = await toPrint({
                path: getPrintPrefix(this.printId)
            });
            if (res && res.code === 1) {
                this.complete();
            } else {
                this.changeTipPage({ message: '打印机出故障，请稍后重试' });
            }
        },
        async complete () {
            // 打印完成调用 办理完成结果
            const res = await proveComplete({});
            if (res && res.code === '0') {
                this.relationCompleteRes = res.result;
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        }
    },
    mounted () {
        this.setSteps([
            {
                name: '身份识别',
                isActive: true,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'process'
            },
            {
                name: '输入亲属身份证号',
                isActive: false,
                visible: true,
                key: 'acceptance'
            },
            {
                name: '核对信息',
                isActive: false,
                visible: true,
                key: 'modelPreview'
            },
            {
                name: '核对信息',
                isActive: false,
                visible: false,
                key: 'personInfo'
            },
            {
                name: '核对信息',
                isActive: false,
                visible: false,
                key: 'print'
            },
            {
                name: '完成办理',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};

</script>
