<template>
    <div>
        <div class="kd-page-title">
            请输入亲属身份证号
        </div>
        <div class="kd-page-content-wrapper">
            <kd-full-input
                :ime-type="3"
                class="inputClass"
                placeholder="请输入身份证号"
                :watch-data="getValue"
                :limit-number="18"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import { getProvingResult } from '../../../../../service/human/prove';
import { checkIdNum } from '../../../../../utils/tools';
import { keyboardHide } from '@/service/common';

export default {
    name: 'RelationshipAcceptance',
    data () {
        return {
            peopleIdNum: ''
        };
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('relation', ['setRelationRes']),
        getValue (value) {
            this.peopleIdNum = value;
        },
        async nextStep () {
            if (checkIdNum(this.peopleIdNum)) {
                const res = await getProvingResult({
                    relationIdNum: this.peopleIdNum
                });
                if (res && res.code === '0') {
                    this.setRelationRes(res.result);
                    this.changeStep('down');
                } else if (res) {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            } else {
                this.errorMsg = '身份证格式书写有误';
            }
        }
    },
    beforeDestroy () {
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .inputClass {
        display: inline-block;
        height: 50px;
        width: 50vw;
        line-height: 50px;
        font-size: 26px;
        color: #636F93;
        margin: 118px 220px auto;
    }
</style>
